.new-collections{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}
.new-collections h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}
.new-collections hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}
.collections{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    row-gap: 30px;
    margin-bottom: 30px;
    justify-items: center;
}